<template>
  <div class="modal" @click.stop="closeModal">
    <div class="modal__close" @click.stop="closeModal"></div>
    <div class="modal__content">
      <img :src="imageSrc" alt="image" @click.stop />
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  methods: {
    ...mapMutations({
      closeModal: "popUp/closeModal",
    }),
  },
  computed: {
    ...mapState({
      imageSrc: (state) => state.popUp.imageSrc,
    }),
  },
};
</script>

<style lang="scss">
.modal {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 5;
  &__close {
    background: #111;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 5;
    left: 20px;
    top: 15px;
    width: 30px;
    height: 30px;
    @media (max-width: 375.98px) {
      left: 15px;
    }
    &:after,
    &:before {
      content: "";
      display: flex;
      position: absolute;
      align-items: center;
      justify-content: center;
      width: 80%;
      height: 2px;
      background-color: rgb(173, 173, 173);
    }
    &:after {
      transform: rotate(-45deg);
    }
    &:before {
      transform: rotate(45deg);
    }
    &:hover {
      border-color: rgb(94, 94, 94);
      &:after,
      &:before {
        background-color: rgb(94, 94, 94);
      }
    }
  }
  &__content {
    flex: 0 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    & img {
      flex: 0 1 auto;
      border-radius: 10px;
      box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.7);
      max-width: 100vw;
      max-height: 100vh;
      object-fit: cover;
      object-position: center;
    }
  }
}
</style>