<template>
  <div class="main">
    <add-form :class="['addForm', isOpenForm ? 'open' : '']"></add-form>
    <item-list v-if="itemsCount" class="item__list"></item-list>
    <div v-else class="zero">
      <h2>У вас нет желаний =(</h2>
    </div>
    <modal v-if="isOpenModal"></modal>
  </div>
</template>

<script>
import AddForm from "@/components/AddForm";
import ItemList from "@/components/ItemList";
import Modal from "@/components/Modal";
import { mapState } from "vuex";
export default {
  components: { AddForm, ItemList, Modal },
  computed: {
    ...mapState({
      isOpenForm: (state) => state.form.isOpen,
      itemsCount: (state) => state.list.items.length,
      isOpenModal: (state) => state.popUp.isOpen,
    }),
  },
};
</script>

<style lang="scss" scoped>
.main {
  display: flex;
  justify-content: space-between;
}
.addForm {
  position: sticky;
  top: 24px;
  flex: 0 0 332px;
  height: 440px;
  @media (max-width: 992.98px) {
    padding: 70px 24px 24px 24px;
    overflow-y: auto;
    position: fixed;
    display: block;
    z-index: 3;
    top: 0;
    left: -110%;
    width: 100%;
    height: 100%;
    transition: left 0.6s ease;
    &.open {
      left: 0;
    }
  }
}
.item__list {
  flex: 0 0 1044px;
  @media (max-width: 1440px) {
    flex-shrink: 1;
  }
}
.zero {
  padding: 20px 0;
  text-align: center;
  font-size: 24px;
  flex: 1;
  margin: 0 0 0 16px;
  font-weight: 600;
}
</style>