export const form = {
  state: () => ({
    isOpen: false,
    newItem: {
      id: '',
      title: '',
      description: '',
      imageSrc: '',
      price: '',
    },
    scrollPosition: 0,
  }),
  getters: {
    activeBtn(state) {
      return state.newItem.title && state.newItem.imageSrc && state.newItem.price;
    },
  },
  mutations: {
    validate(state, event) {
      if (!event.target.value) {
        event.target.parentElement.classList.add("error");
      } else {
        event.target.parentElement.classList.remove("error");
      }
    },
    setId(state) {
      state.newItem.id = Date.now();
    },
    setTitle(state, value) {
      state.newItem.title = value
    },
    setDescription(state, value) {
      state.newItem.description = value
    },
    setImageUrl(state, value) {
      state.newItem.imageSrc = value.trim()
    },
    setPrice(state, value) {
      state.newItem.price = value.trim()
    },
    setForm(state) {
      state.isOpen = !state.isOpen

      if (state.isOpen) {
        state.scrollPosition = window.pageYOffset

        document.body.setAttribute('data-body-scroll-fix', state.scrollPosition)
        document.body.style.top = `-${state.scrollPosition}px`
        document.body.classList.add("lock");
      } else {
        state.scrollPosition = document.body.getAttribute('data-body-scroll-fix')

        document.body.removeAttribute('data-body-scroll-fix')
        document.body.style.top = ''
        document.body.classList.remove("lock");
        window.scroll(0, state.scrollPosition);
      }

    },
    clearForm(state) {
      state.newItem = {
        title: "",
        description: "",
        imageSrc: "",
        price: "",
      };
    },
  },
  actions: {
    createNewItem({ state, commit }) {
      commit('setId')
      commit('list/createItem', state.newItem, { root: true })
      commit('clearForm')
      screen.width <= 992 ? commit('setForm') : ''
    }
  },
  namespaced: true,
}