<template>
  <input
    :type="type"
    class="input"
    :placeholder="myPlaceholder"
    :value="modelValue"
    @input="updateInput"
  />
</template>

<script>
export default {
  name: "form-input",
  props: {
    type: {
      type: String,
      default: "text",
    },
    modelValue: [String, Number],
    myPlaceholder: {
      type: String,
      required: true,
    },
    error: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    updateInput(event) {
      this.$emit("update:modelValue", event.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.input {
  padding: 10px 15px 11px;
  height: 36px;
  background-color: #fffefb;
  border: 1px solid transparent;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  font-size: 12px;
  line-height: 15px;
  color: #b4b4b4;
  &:hover,
  &:focus {
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
  }
  &:focus {
    border: 1px solid #0d6efd;
  }
  &:valid {
    color: #3f3f3f;
  }
  &::placeholder {
    color: #b4b4b4;
  }
}
</style>