<template>
  <label class="label">
    <span :class="[isRequired ? 'required' : '']">
      <slot></slot>
    </span>
  </label>
</template>

<script>
export default {
  name: "form-label",
  props: {
    isRequired: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style lang="scss">
.label {
  font-size: 10px;
  line-height: 13px;
  letter-spacing: -0.02em;
  color: #49485e;
}
.required {
  position: relative;
  &::after {
    content: "";
    display: block;
    width: 4px;
    height: 4px;
    background: #ff8484;
    border-radius: 4px;
    position: absolute;
    top: 0;
    right: -4px;
  }
}
</style>