<template>
  <header class="header">
    <div class="header__body">
      <div class="header__group">
        <a
          :class="[
            'header__add-button',
            'header__add-button_theme',
            isOpenForm ? 'active' : '',
          ]"
          @click.prevent="setForm"
        ></a>
        <h1 :class="['header__title', isOpenForm ? 'active' : '']">
          Новое желание
        </h1>
      </div>
      <form-select
        :options="options"
        v-if="!isOpenForm"
        :model-value="sort"
        @update:model-value="sortItems"
      ></form-select>
    </div>
  </header>
</template>

<script>
import FormSelect from "./UI/FormSelect.vue";
import { mapMutations, mapState } from "vuex";
export default {
  components: { FormSelect },
  methods: {
    ...mapMutations({
      setForm: "form/setForm",
      sortItems: "list/sortItems",
    }),
  },
  computed: {
    ...mapState({
      isOpenForm: (state) => state.form.isOpen,
      sort: (state) => state.list.sort,
      options: (state) => state.list.options,
    }),
  },
};
</script>

<style lang="scss" scoped>
$min-width: 375.98;
$mw: 1440;
$md2: 992.98;
$md3: 767.98;
$md4: 479.98;
$BG-color: rgba(255, 254, 251, 0.8);
$color: #3f3f3f;

.header {
  margin: 0 0 16px 0;
  padding: 32px 0 0 0;
  @media (max-width: $md2+px) {
    margin: 0 -25px 16px -25px;
    position: sticky;
    top: 0px;
    left: 0;
    z-index: 4;
    padding: 10px 0;
    background: rgba($color: $BG-color, $alpha: 1);
    box-shadow: 0px 0px 4px #3f3f3f2f;
    z-index: 4;
  }
  @media (max-width: $md4+px) {
    margin: 0 -20px 16px -20px;
  }
  @media (max-width: $min-width+px) {
    margin: 0 -15px 16px -15px;
  }
  &__body {
    min-height: 36px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: $md2+px) {
      padding: 0 25px;
    }
    @media (max-width: $md4+px) {
      padding: 0 20px;
    }
    @media (max-width: $min-width+px) {
      padding: 0 15px;
    }
  }
  &__group {
    display: flex;
    align-items: center;
  }
  &__title {
    font-weight: 600;
    font-size: 28px;
    line-height: 35px;
    @media (max-width: $md2+px) {
      font-size: 22px;
      line-height: 29px;
    }
    @media (max-width: $md3+px) {
      font-size: 18px;
      line-height: 25px;
    }
    @media (max-width: $md2+px) {
      position: fixed;
      width: calc(100% - 40px);
      top: 15px;
      left: 110%;
      transition: left 0.6s ease;
      text-align: center;
      &.active {
        left: 20px;
        @media (max-width: $min-width+px) {
          left: 15px;
        }
      }
    }
  }
  &__add-button {
    cursor: pointer;
    margin: 0 10px 0 0;
    @media (max-width: $md4+px) {
      margin: 0;
    }
    display: none;
    width: 20px;
    height: 20px;
    position: relative;
    z-index: 4;
    display: none;
    @media (max-width: $md2+px) {
      display: block;
    }
    &:before,
    &::after {
      content: "";
      display: block;
      width: 20px;
      height: 2px;
      background: $color;
      position: absolute;
      top: 50%;
      left: 0;
      transition: all 0.8s ease;
    }
    &::after {
      transform: rotate(-90deg);
    }
    &.active {
      &:before {
        transform: rotate(-45deg);
      }
      &::after {
        transform: rotate(45deg);
      }
    }
  }
}
</style>