export const list = {
  state: () => ({
    items: [
      {
        id: 1,
        title: "ESP Alexi Laiho Arrow Head",
        description: "Электрогитара c 6 струнами, Floyd Rose Original, 24 лада, мензура 25.5\", сквозной гриф, активная электроника, корпус: ольха, гриф: клен, накладка: ebony.",
        price: "50000",
        imageSrc:
          "https://img.audiofanzine.com/images/u/product/normal/esp-alexi-arrow-head-64733.jpg",
      },
      {
        id: 2,
        title: "Ibanez JEM77P-BFP",
        description: "Электрогитара оборудована грифом из клена и ореха (5 кусков) с титановым усилением. Профиль грифа - Wizard. Гриф имеет оригинальную инкрустацию. Крепление грифа к деке выполнено на болтах. Инструмент пользуется популярностью среди гитаристов, предпочитающих насыщенное, певучее, роковое звучание электрогитары. На деку установлен подвижный тремоло-бридж с рычагом, модель - Edge-Zero II. Количество ладов грифа - 24 (Jumbo), корпус гитары украшен синим графическим рисунком. Инструмент комплектуется фирменным кейсом.",
        price: "99000",
        imageSrc: "https://www.soundaffects.com/images/products/medium/1541439397-23671700.jpg",
      },
      {
        id: 5,
        title: "Porsche 911 Targa 4S",
        description: `Объем двигателя	2 981 см³, Максимальный крутящий момент (Нм)	530 Нм, Мощность - 450 л.с., Разгон 0-100 км/ч - 3,8 сек, Максимальная скорость - 304 км/ч`,
        price: "11400000",
        imageSrc:
          "https://tarantas.news/uploads/images/2020/06/02/Targa_006.jpg",
      },
      {
        id: 6,
        title: "iPhone 13",
        description: `Технология ProMotion с адаптивной частотой обновления до 120 Гц
        Поддержка HDR
        Технология True Tone
        Широкий цветовой охват (P3)
        Тактильный отклик при нажатии
        Контрастность 2 000 000:1 (стандартная)
        Яркость до 1000 кд/м² (стандартная); до 1200 кд/м² при просмотре контента в формате HDR`,
        price: "240000",
        imageSrc:
          "https://www.ixbt.com/img/r30/00/02/46/28/Snimok-jekrana-20211021-v-16.47.19.png",
      },
    ],
    sort: 'default',
    options: [
      {
        text: 'По умолчанию',
        value: 'default',
      },
      {
        text: 'По цене ↑',
        value: 'priceToHight',
      },
      {
        text: 'По цене ↓',
        value: 'priceToLow',
      },
      {
        text: 'По названию ↑',
        value: 'nameToHight',
      },
      {
        text: 'По названию ↓',
        value: 'nameToLow',
      },
    ],
  }),
  getters: {
    setSort(state) {
      if (state.sort === "default") {
        return state.items.sort((a, b) => a.id - b.id);
      } else if (state.sort === "priceToHight") {
        return state.items.sort((a, b) => b.price - a.price);
      } else if (state.sort === "priceToLow") {
        return state.items.sort((a, b) => a.price - b.price);
      } else if (state.sort === "nameToHight") {
        return state.items.sort((a, b) =>
          a.title.toLowerCase() > b.title.toLowerCase() ? -1 : 1
        );
      } else if (state.sort === "nameToLow") {
        return state.items.sort((a, b) =>
          a.title.toLowerCase() > b.title.toLowerCase() ? 1 : -1
        );
      }
    },
  },
  mutations: {
    createItem(state, item) {
      state.items.push(item);
    },
    removeItem(state, item) {
      state.items = state.items.filter((card) => card.id !== item.id);
    },
    clickItem(state, item) {
      state.isOpen = true;
      state.imageSrc = item.imageSrc;
      document.body.classList.add("lock");
    },
    sortItems(state, sortValue) {
      state.sort = sortValue;
    },
  },
  actions: {
    openModal(context, item) {
      context.commit('popUp/setModal', item.imageSrc, { root: true })
    },
  },
  namespaced: true,
}