<template>
  <form class="add-form" @submit.prevent="createNewItem">
    <div class="add-form__content">
      <form-label class="form-label" :isRequired="true"
        >Наименование товара</form-label
      >
      <form-input
        :model-value="title"
        @update:model-value="setTitle"
        class="form-input"
        :myPlaceholder="'Введите наименование товара'"
        @blur="validate"
      ></form-input>
    </div>
    <div class="add-form__content">
      <form-label class="form-label">Описание товара</form-label>
      <form-textarea
        :model-value="description"
        @update:model-value="setDescription"
        :myPlaceholder="'Введите описание товара'"
        class="add-form__textarea"
      ></form-textarea>
    </div>
    <div class="add-form__content">
      <form-label class="form-label" :isRequired="true"
        >Ссылка на изображение товара</form-label
      >
      <form-input
        :type="'url'"
        :model-value="imageSrc"
        @update:model-value="setImageUrl"
        class="form-input"
        :myPlaceholder="'Введите ссылку '"
        @blur="validate"
      ></form-input>
    </div>
    <div class="add-form__content">
      <form-label class="form-label" :isRequired="true">Цена товара</form-label>
      <form-input
        :type="'number'"
        :model-value="price"
        @update:model-value="setPrice"
        class="form-input"
        :myPlaceholder="'Введите цену'"
        @blur="validate"
      ></form-input>
    </div>
    <div class="add-form__content">
      <form-button type="submit" :isDisabled="!activeBtn" class="add-form__btn"
        >Добавить товар</form-button
      >
    </div>
  </form>
</template>

<script>
import FormButton from "@/components/UI/FormButton.vue";
import FormInput from "@/components/UI/FormInput.vue";
import FormLabel from "@/components/UI/FormLabel.vue";
import FormTextarea from "@/components/UI/FormTextarea.vue";
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
export default {
  components: {
    FormButton,
    FormInput,
    FormLabel,
    FormTextarea,
  },
  methods: {
    ...mapMutations({
      setTitle: "form/setTitle",
      setDescription: "form/setDescription",
      setImageUrl: "form/setImageUrl",
      setPrice: "form/setPrice",
      validate: "form/validate",
    }),
    ...mapActions({
      createNewItem: "form/createNewItem",
    }),
  },
  computed: {
    ...mapState({
      title: (state) => state.form.newItem.title,
      description: (state) => state.form.newItem.description,
      imageSrc: (state) => state.form.newItem.imageSrc,
      price: (state) => state.form.newItem.price,
    }),
    ...mapGetters({
      activeBtn: "form/activeBtn",
    }),
  },
};
</script>

<style lang="scss" scoped>
.add-form {
  padding: 24px;
  display: flex;
  flex-direction: column;
  background: #fffefb;
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.04),
    0px 6px 10px rgba(0, 0, 0, 0.02);
  border-radius: 4px;
  &__content {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0px;
    }
    @media (max-width: 992px) {
      margin-left: auto;
      margin-right: auto;
      max-width: 332px;
    }
    &.error {
      & input,
      & textarea {
        background: #fffefb;
        border: 1px solid #ff8484;
      }
      position: relative;
      &::after {
        content: "Поле является обязательным";
        position: absolute;
        bottom: -12px;
        left: 0;
        font-size: 8px;
        line-height: 10px;
        letter-spacing: -0.02em;
        color: #ff8484;
      }
    }
  }
  &__textarea {
    resize: none;
    width: 100%;
    height: 108px;
  }
  &__btn {
    margin: 8px 0 0 0;
    width: 100%;
  }
}
.form-label {
  margin: 0 0 4px 0;
}
</style>