<template>
  <select class="filter" :value="modelValue" @change="updateValue">
    <option v-for="option in options" :value="option.value" :key="option.value">
      {{ option.text }}
    </option>
  </select>
</template>

<script>
export default {
  props: {
    modelValue: [String, Number],
    options: { type: Array, required: true },
  },
  methods: {
    updateValue(event) {
      this.$emit("update:modelValue", event.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.filter {
  padding: 10px 16px 11px;
  height: 36px;
  width: 121.49px;
  -webkit-appearance: none;
  background: url("../../assets/arrow.svg") 98px 16px / 8px no-repeat, #fffefb;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  font-size: 12px;
  line-height: 15px;
  color: #b4b4b4;
  &:hover,
  &:focus {
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
  }
}
</style>