        <img src="@/assets/delete.svg" alt="delete" class="item__icon" />
      </div>
      <div class="item__image">
        <img :src="item.imageSrc" alt="item-image" />
      </div>
      <div class="item__body">
        <h2 class="item__title content__title_theme">
          {{
            item.title
              .trim()
              .split(" ")
              .map((word) => word[0].toUpperCase() + word.slice(1))
              .join(" ")
          }}
        </h2>
        <p class="item__text content__text_theme">
          {{
            item.description
              .trim()
              .split(". ")
              .map((word) => word[0].toUpperCase() + word.slice(1))
              .join(". ")
          }}
        </p>
        <span class="item__price content__price_theme">{{
          item.price.replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, "$1 ")
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  methods: {
    ...mapMutations({
      removeItem: "list/removeItem",
    }),
    ...mapActions({
      openModal: "list/openModal",
    }),
  },
};
</script>

