export const popUp = {
  state: () => ({
    isOpen: false,
    imageSrc: '',
    scrollPosition: 0,
  }),
  getters: {},
  mutations: {
    setModal(state, imageSrc) {
      state.isOpen = !state.isOpen
      state.imageSrc = imageSrc
      state.scrollPosition = window.pageYOffset

      document.body.setAttribute('data-body-scroll-fix', state.scrollPosition)
      document.body.style.top = `-${state.scrollPosition}px`
      document.body.classList.add("lock");
    },
    closeModal(state) {
      state.isOpen = !state.isOpen
      state.scrollPosition = document.body.getAttribute('data-body-scroll-fix')

      document.body.removeAttribute('data-body-scroll-fix')
      document.body.style.top = ''
      document.body.classList.remove("lock");
      window.scroll(0, state.scrollPosition);
    },
  },
  actions: {},
  namespaced: true,
}